@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";
@keyframes bounceInScroll {
  from,
  60%,
  75%,
  90%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  from {
    opacity: 0;
    transform: translate3d(150px, 0, 0);
  }

  60% {
    opacity: 1;
    transform: translate3d(-15px, 0, 0);
  }

  75% {
    -webkit-transform: translate3d(10px, 0, 0);
    transform: translate3d(10px, 0, 0);
  }

  90% {
    -webkit-transform: translate3d(-5px, 0, 0);
    transform: translate3d(-5px, 0, 0);
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
.bounceScroll {
  animation-name: bounceInScroll;
  animation-duration: 1s;
  animation-iteration-count: 8;
}
@include mobile {
  .com-introduce {
    font-family: "Montserrat", sans-serif;
    background-color: white;
    margin-top: 9.9vh;
    > .wrapper-video {
      position: relative;

      video {
        width: 100%;

        object-fit: cover;
      }
      .img-bg {
        width: 100%;
      }
      .div-bg {
        width: 100%;
        height: 100vw;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
      }
      .logo {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
    .wrapper-content {
      position: relative;
      display: flex;
      padding: $pdMobile;
      > img {
        width: unset;
        margin: 50px auto;
        display: none;
      }
      .block-content {
        max-width: 600px;
        .title {
          font-size: 1.5rem;
          font-weight: 800;
          margin-bottom: 1rem;
        }
        .content {
          font-size: 1rem;
          font-family: "Lato", sans-serif;
          ul {
            list-style-type: unset;
            padding-left: 1rem;
            margin-top: 1rem;
            li {
              // `
              text-transform: capitalize;
            }
          }
        }
        .second-content {
          margin-top: 3rem;
        }
      }

      .block-scroll {
        position: absolute;
        bottom: 0.5rem;
        right: 1.5rem;
        display: flex;
        font-size: 10px;
        align-items: center;
        transform: rotate(-90deg);
        transform-origin: right;

        .hr-scroll {
          width: 100px;
          height: 1.5px;
          background-color: $hong_color;
          margin-right: 0.75rem;
        }
      }
    }
    .block-content-sroll {
      display: none;
    }
    .block-scroll1 {
      position: sticky;
      bottom: 10%;
      display: flex;
      font-size: 10px;
      font-weight: 500;
      align-items: center;
      transform: rotate(-90deg);
      transform-origin: right;
      width: fit-content;
      margin-right: 1rem;
      float: right;
      text-transform: uppercase;
      .hr-scroll {
        width: 100px;
        height: 1.5px;
        background-color: $hong_color;
        margin-right: 0.75rem;
        @include desktop2k {
          height: 4px;
        }
      }
    }
  }
}

@include desktop {
  .com-introduce {
    margin-top: unset;
    position: relative;
    height: 100vh;
    overflow: hidden;
    > .wrapper-video {
      position: sticky;
      top: 0;
      height: 100vh;
      video {
        height: 100%;
      }
      .img-bg {
        position: absolute;
        left: 50%;
        width: 50%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
      .div-bg {
        position: absolute;
        left: 0;
        width: 100%;
        height: 100%;
      }
      .logo {
        display: none;
      }
    }

    .wrapper-content {
      position: absolute;
      top: 0;
      width: 50%;
      height: 100vh;
      background-color: white;
      padding: $pdDesk;
      display: flex;
      flex-direction: column;
      > img {
        margin: 10vh auto;
        display: unset;
        @include desktop2k {
          width: 15vw;
        }
      }
      .block-content {
        @include desktop2k {
          max-width: 30vw;
        }
        &.top {
          bottom: unset;
          top: 9.9vh;
          //Not homepage
          .first-content {
            .title {
              font-size: 3.5rem;
              @include desktop2k {
                font-size: 2rem;
              }
              @include desktop4k {
                font-size: 4rem;
              }
            }
            .content {
              margin-top: 3rem;
              font-size: 1.5rem;
              @include desktop4k {
                font-size: 2.25rem;
              }
            }
          }
        }
        .first-content {
          .title {
            font-size: 1.85rem;
            margin-bottom: 0;
            @include desktop2k {
              font-size: 2rem;
            }
            @include desktop4k {
              font-size: 3rem;
            }
            @include textGlitch("titleIntro", 17, black, rgba(0, 0, 0, 0.001), rgba(255, 0, 0, 0.3), rgba(0, 0, 255, 0.3), 500, 200);
          }
          .content {
            margin-top: 3rem;
            font-size: 1.5rem;
            @include desktop2k {
              font-size: 1.75rem;
            }
          }
        }
        .second-content {
          display: none;
        }
      }

      .block-scroll {
        position: absolute;
        bottom: 5rem;
        left: -7.5rem;
        right: unset;

        .hr-scroll {
          width: 100px;
          height: 1.5px;
          background-color: $hong_color;
          margin-right: 0.75rem;
        }
      }
    }
    .block-content-sroll {
      display: block;
      height: 100vh;
      width: 50%;
      background-color: white;
      position: absolute;
      padding: $pdDesk;

      .title {
        width: 80%;
        font-size: 60px;
        opacity: 0;
        margin-bottom: 1.5rem;
        @include textGlitch("titleIntroTwo", 17, black, rgba(0, 0, 0, 0.001), rgba(255, 0, 0, 0.3), rgba(50, 50, 68, 0.3), 600, 300);
      }
      .content {
        font-size: 20px;
        opacity: 0;
      }
    }
    .block-scroll1 {
      left: 2.5vw;
      top: 105%;
      bottom: unset;
      float: left;
      width: fit-content;
      transform-origin: left;
      @include desktop2k {
        font-size: 1rem;
      }
    }
  }
}
